import React from 'react'
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { Button, Grid, Grow, Icon, makeStyles, Paper, Typography } from '@material-ui/core'
import IconButton from '@material-ui/core/IconButton';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import PropTypes from 'prop-types';
import Box from '@material-ui/core/Box';
import AppBar from '@material-ui/core/AppBar';
import resumeData from './../../components/utils/resumeData';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

const useStyles = makeStyles({
    root: {
        maxWidth: 345,
    },
    media: {
        height: 140,
    },
});

function TabPanel(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = (props) => {
        setOpen(props);
        console.log(props);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <Card className={classes.root}>
                <CardActionArea onClick={() => handleClickOpen(props)}>
                    <CardMedia
                        className={classes.media}
                        image={require('./../../assets/images/' + props.image).default}
                        title="Contemplative Reptile"
                    />
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="h2" noWrap="true">
                            {props.title}
                        </Typography>
                        <Typography variant="body2" color="textSecondary" component="p" noWrap="true">
                            {props.description}
                        </Typography>
                    </CardContent>
                </CardActionArea>
                <CardActions disableSpacing>
                    <IconButton aria-label="add to favorites">
                        <FavoriteIcon />
                    </IconButton>
                    <IconButton aria-label="share">
                        <ShareIcon />
                    </IconButton>
                </CardActions>
            </Card>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">{open.title}</DialogTitle>
                <img src={require('./../../assets/images/' + props.image).default} />
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {open.description}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <IconButton aria-label="add to favorites" onClick={handleClose} color="primary" autoFocus>
                        <FavoriteIcon />
                    </IconButton>
                    <IconButton aria-label="share" onClick={handleClose} color="primary" autoFocus>
                        <ShareIcon />
                    </IconButton>
                </DialogActions>
            </Dialog>

        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

const Portfolio = () => {
    const [value, setValue] = React.useState("All");

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div>
            <Paper position="static" color="default">
                <Tabs value={value} onChange={handleChange} aria-label="simple tabs example" indicatorColor="primary">
                    <Tab label="All" value="All" />
                    {[...new Set(resumeData.projects.map(item => item.tag))].map(tag => (
                        <Tab label={tag} value={tag} />
                    ))}
                </Tabs>
            </Paper>
            <Grid container spacing={2}>
                {resumeData.projects.map(project => (
                    <>
                        {value == project.tag || value == 'All' ? (
                            <Grid item xs={6} md={4}>
                                <Grow in timeout={1000}>
                                    <TabPanel value={value} index={project.tag} image={project.image} title={project.title} description={project.description}>
                                    </TabPanel>
                                </Grow>
                            </Grid>
                        ) :
                            null
                        }
                    </>
                ))}
            </Grid>
        </div>
    );
}

export default Portfolio
