import { Button, Grid, Icon, makeStyles, Paper, Typography } from '@material-ui/core'
import React from 'react'
import resumeData from './../../components/utils/resumeData';
import TimelineItem from '@material-ui/lab/TimelineItem';
import CustomTimeline, { CustomTimelineSeparator } from './../../components/Timeline/Timeline'
import WorkIcon from '@material-ui/icons/Work';
import TimelineContent from '@material-ui/lab/TimelineContent';
import LinearProgress from '@material-ui/core/LinearProgress';
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        width: '25ch',
    },
}));

const Resume = () => {
    return (
        <React.Fragment>
            <div style={{ padding: '2rem' }}>
                <Grid container>
                    <Grid item>
                        <Typography variant='h6'>
                            A PROPOS DE MOI
                    </Typography>
                    </Grid>
                </Grid>
                <Grid container>
                    <Grid item>
                        <Typography variant='subtitle2' style={{ whiteSpace:'pre-line' }}>
                            {resumeData.about}
                        </Typography>
                    </Grid>
                </Grid>

                <Grid container>
                    <Grid item>
                        <Typography variant='h6' style={{ marginTop: '3rem' }}>
                            RESUME
                    </Typography>
                    </Grid>
                </Grid>
                <Grid container>
                    <Grid item xs={12} md={6}>
                        <CustomTimeline title={'EXPERIENCES'} icon={<WorkIcon />} >
                            {resumeData.experiences.map((experience) => (
                                <TimelineItem key={experience.id}>
                                    <CustomTimelineSeparator />
                                    <TimelineContent>
                                        <Typography variant='h6'>
                                            {experience.titre}
                                        </Typography>
                                        <Typography variant="caption">
                                            {experience.date}
                                        </Typography>
                                        <Typography variant='body2'>
                                            {experience.description}
                                        </Typography>
                                    </TimelineContent>
                                </TimelineItem>
                            ))}
                        </CustomTimeline>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <CustomTimeline title={'FORMATIONS'} icon={<WorkIcon />} >
                            {resumeData.educations.map((education) => (
                                <TimelineItem key={education.id}>
                                    <CustomTimelineSeparator />
                                    <TimelineContent>
                                        <Typography variant='h6'>
                                            {education.titre}
                                        </Typography>
                                        <Typography variant="caption">
                                            {education.date}
                                        </Typography>
                                        <Typography variant='body2'>
                                            {education.description}
                                        </Typography>
                                    </TimelineContent>
                                </TimelineItem>
                            ))}
                        </CustomTimeline>
                    </Grid>
                </Grid>
                <Grid container>
                    <Grid item>
                        <Typography variant='h6' style={{ textDecoration: 'underline', marginBottom: '2rem', textDecorationColor: '#bdbdbd' }}>
                            Mes Services
                    </Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <Grid container spacing={2} justify='space-around'>
                            {resumeData.services.map(service => (
                                <Grid item xs={12} sm={6} md={3} key={service.id}>
                                    <Paper elevation={0} style={{ backgroundColor: 'white', padding: '1rem', textAlign: 'center' }}>
                                        <Icon>{service.icon}</Icon>
                                        <Typography variant='body1' noWrap={true}>
                                            <strong>
                                                {service.title}
                                            </strong>
                                        </Typography>
                                        <Typography variant='subtitle2'>
                                            {service.description}
                                        </Typography>
                                    </Paper>
                                </Grid>
                            ))}
                        </Grid>
                    </Grid>
                </Grid>
                <Grid container style={{ marginTop: '3rem' }} spacing={3}>
                    {resumeData.skills.map((skill) => (
                        <Grid item xs={12} sm={6} md={6} key={skill.id}>
                            <Icon></Icon>
                            <Typography variant='h6' style={{ textDecoration: 'underline', textDecorationColor: '#bdbdbd' }}>
                                {skill.title}
                            </Typography>
                            {skill.detail.map(element => (
                                <div key={element.id}>
                                    <Typography style={{ marginBottom: '0', marginTop: '2rem' }}>
                                        {element.title}
                                    </Typography>
                                    <LinearProgress color="primary" variant="determinate" value={element.note} style={{ width: '100%' }} />
                                </div>
                            ))}
                        </Grid>
                    ))}
                </Grid>

                <Grid container style={{ marginTop: '3rem' }} spacing={2}>
                    <Grid item xs={12} md={7}>
                        <Typography variant='h6'>
                            Contact form
                        </Typography>
                        <Grid item xs={12}>
                            <Grid container spacing={2}>
                                <Grid item xs={12} sm={6}>
                                    <TextField
                                        label="Nom"
                                        helperText="Nom"
                                        fullWidth
                                        margin="dense"
                                        variant="outlined"
                                    />
                                </Grid>
                                <Grid item xs={12} sm={6}>
                                    <TextField
                                        label="Email"
                                        helperText="Email"
                                        fullWidth
                                        margin="dense"
                                        variant="outlined"
                                    />
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <TextField multiline rows={4}
                                        label="Message"
                                        helperText="Message"
                                        fullWidth
                                        margin="dense"
                                        variant="outlined"
                                    />
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <Button variant="contained" style={{ backgroundColor: '#2196f3', color: 'white' }}>
                                        Envoyer
                                </Button>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12} md={5}>
                        <Grid container spacing={2}>
                            <Grid item>
                                <Typography variant='h6'>
                                    Contact information
                                </Typography>
                                <Grid container spacing={2}>
                                    <Grid item>
                                        <Typography>
                                            <span>Adress: </span> Dakar, Senegal
                                        </Typography>
                                    </Grid>
                                    <Grid item>
                                        <Typography>
                                            <span>Adress: </span> Dakar, Senegal
                                        </Typography>
                                    </Grid>
                                    <Grid item>
                                        <Typography>
                                            <span>Phone: </span> +221 77 568 79 61
                                        </Typography>
                                    </Grid>
                                    <Grid item>
                                        <Typography>
                                            <span>Adress: </span> Dakar, Senegal
                                        </Typography>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </div>
        </React.Fragment>
    )
}

export default Resume
