import { Box, Button, Paper, Typography } from '@material-ui/core'
import './Profile.css';
import CustomTimeline, { CustomTimelineSeparator } from './../Timeline/Timeline'
import React from 'react';
import resumeData from './../utils/resumeData';
import PersonOutlineOutlinedIcon from '@material-ui/icons/PersonOutlineOutlined';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineContent from '@material-ui/lab/TimelineContent';
import SaveIcon from '@material-ui/icons/Save';

const CustomTimelineItem = (props) =>(
    <TimelineItem>
        <CustomTimelineSeparator />
        <TimelineContent>
            {props.link ? (
                <Typography className='timelineItem_text'>
                    <span>{props.title}:</span>
                    <a href={props.link} > {props.text}</a>
                </Typography>
            ):(
                <Typography className='timelineItem_text'>
                    <span>{props.title}:</span> {props.text}
                </Typography>
            )}
        </TimelineContent>
    </TimelineItem>
)

const Profile = () => {
    return (
        <div className="profile container_shadow">
            <div className="profile_name" style={{padding:'1rem', textAlign:'center'}}>
                <Typography className="name">{resumeData.name}</Typography>
                <Typography className="title">{resumeData.title}</Typography>
            </div>
            <div className="profile_image">
                <img src={require('./../../assets/images/image.jpg').default} alt="" />
            </div>
            <div className="profile_information" style={{padding:'1rem',marginTop:"-4rem"}}>
                <CustomTimeline icon={<PersonOutlineOutlinedIcon />}>   
                    <CustomTimelineItem title='Name' text={resumeData.name} />                 
                    <CustomTimelineItem title='Title' text={resumeData.title} />                 
                    <CustomTimelineItem title='Email' text={resumeData.email} />  
                    {Object.keys(resumeData.socials).map((key) => (
                        <CustomTimelineItem title={key} text={resumeData.socials[key].text} link={resumeData.socials[key].link} key={resumeData.socials[key].id} />  
                    ))}               
                </CustomTimeline>
                <br/>
                <Box  display="flex" justifyContent="center">
                    <Button variant="contained" href="https://github.com/massar77/curriculum_vitae/blob/5878d5c2150a5bbc4449d213dec548d1bb0cd69e/Cv_Pape_Massar_Sow_2021.pdf" style={{ backgroundColor:'#2196f3', color:'white'}} endIcon={<SaveIcon />} size="small">
                        Download CV
                    </Button>
                </Box>
            </div>
        </div>
    )
}

export default Profile
